.header {
  display: flex;
  justify-content: space-between;

  &__social-list {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: row;
    line-height: 0;
    width: max-content;

    * + * {
      margin-left: 15px;
    }

    &--footer {
      margin: 0 auto;
    }
  }

  // &__social-item {
  //   line-height: 0;
  // }

  &__social-link {
    display: block;

    svg {
      border-radius: 50%;
      width: 30px;
      height: 30px;

      @include viewport--sm {
        width: 25px;
        height: 25px;
      }
    }
  }

  span {
    padding: 28px 0;
    letter-spacing: 1.5px;

    @include viewport--sm {
      padding: 18px 0 17px 0;
    }
  }
}
